<template>
    <div id='schedule'>
        <div class="container">
            <b-table hover 
            :items="classes" 
            :fields="fields" 
            id="pilatesclasses"
            :per-page="perPage"
            :current-page="currentPage"
            >
            </b-table>
            
            <b-pagination
                v-model="currentPage"
                :total-rows="rows"
                :per-page="perPage"
                aria-controls="pilatesclasses"
            ></b-pagination>

        </div>
    </div>
</template>
<script>
function adjustnorows(){
               if(window.screen.width<376) {
                  return 5;
               }else if(window.screen.width<769){
                  return 8;
               }else{
                  return 11;
               }
        }

 import db from './firebaseInit'


export default {
    name:'Schedule',
    
    data(){
        return {
            perPage: adjustnorows(),
            currentPage: 1,
            fields:[
                {
                    key:'ora',
                    sortable:true
                },
                {
                    key:'zi',
                    sortable:true
                },
                {
                    key:'clasa',
                    sortable:true
                },
                {
                    key:'antrenor',
                    sortable:true
                },
                {
                    key:'locuri',
                    sortable:true
                }
            ],
            
            classes:[
                    
            ]
        }
    },
    created(){
            db.collection('classes').get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                   
                    const data = {
                        'id':doc.id,
                        'ora':doc.data().ora,
                        'zi':doc.data().zi,
                        'clasa':doc.data().clasa,
                        'antrenor':doc.data().antrenor,
                        'locuri':doc.data().locuri
                    }
                    
                    this.classes.push(data)
                })
            })   
             

         
    },

    computed: {
      rows() {
        return this.classes.length
      }
    },
    
    
}
</script>

<style scoped>

#schedule{
    background-color: #e9dee1!important;
    
}
@media screen and (max-width:376px){
    #schedule{
        display: grid;
        font-size:10px;
       
        height: auto;
    }
    
}
@media screen and (max-width:768px){
    #schedule{
        display: grid;
        font-size:10px;
       
        height: auto;
    }
    
}
#pilatesclasses{
    color:#a07c82;
    
       
}
.b-pagination{
    display:inline-flex;
}


</style>