import Vue from "vue";
import Router from "vue-router";
import Home from '@/components/Home';
import About from '@/components/About';
import Schedule from '@/components/Schedule';
import Subscriptions from '@/components/Subscriptions';
import Account from '@/components/Account';
import Login from '@/components/Login';
import Register from '@/components/Register';
import firebase from 'firebase';
Vue.use(Router);

let router = new Router({
    mode: 'history',
    routes: [
      {
        path: '/',
        name: 'Home',
        component: Home
      },
      {
        path: '/Login',
        name:'Login',
        component:Login,
        meta:{
          requiresGuest: true
        }
      },
      {
        path:'/Register',
        name:'Register',
        component:Register,
        meta:{
          requiresGuest: true
        }
      },
      {
        path: '/About',
        name: 'About',
        component: About
      },{
        path:'/Schedule',
        name:'Schedule',
        component:Schedule
         },
      {
          path:'/Subscriptions',
          name:'Subscriptions',
          component:Subscriptions
      },
      {
        path:'/Account',
        name:'Account',
        component:Account,
        meta:{
          requiresAuth: true
        }
    },
    {
      
      path:'/schedule/'+num,
      name:'Schedule',
      component:Schedule
       }
    ]
  });

var num;
router.beforeEach((to, from, next) =>{
//check for requiredauth guard
//nav guards
// var nr = ParseInt(to.params.noClasses,10);
// if(nr>0){
// next({
//   path:'/schedule/'+nr
// })
// }


if(to.matched.some(record => record.meta.requiresAuth)){
      //check if the use is NOT logged in
      if(!firebase.auth().currentUser){
            //go to login
            next({
              path:'/login',
              query:{
                redirect: to.fullPath
              }
            });
                 }
                 else{
                   //logged in
                   //call next 
                   next();
                 }
            }
            else if(to.matched.some(record => record.meta.requiresGuest)){
              //if they're logged in => redirect to account
              if(firebase.auth().currentUser){
                //go to login
                next({
                  path:'/account',
                  query:{
                    redirect: to.fullPath
                  }
                });
                     }
                     else{
                      
                       next();
                     }
                } 
            else{
              next();
            }
   });
  

   export default router;

