<template>
         <!-- Default form login -->
         <div class="container">
  <form>
    <p class="h4 text-center mb-4">Log in</p>
    <label for="defaultFormLoginEmailEx" class="grey-text">Email</label>
    <input type="email" id="email" class="form-control" v-model="email" />
    <br/>
    <label for="defaultFormLoginPasswordEx" class="grey-text">Parola</label>
    <input type="password" id="password" class="form-control" v-model="password" />
    <div class="text-center mt-4">
      <button class="btn btn-indigo" type="submit" v-on:click="login">Login</button>
    </div>
  </form>
  <!-- Default form login -->
         </div>
</template>

<script>
import firebase from 'firebase';

export default {
    name:'Login',
     data: function(){
        return {
            email:'',
            password:''
        };
        },
    methods:{
        login: function(e){
           firebase.auth().signInWithEmailAndPassword(this.email,this.password)
           .then(() => {
               this.$router.push('/Account');
           },
           err => {
               alert(err.message);
           });
            e.preventDefault();
           }
          
    }}
</script>

<style scoped>
.container{
    max-width: 350px;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding:20px;
    
}
.btn, .btn-indigo{
    background-color:#a07c82;
    color:white;
}
</style>